/*----START CONTACT----*/

.form-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  background: rgba(4, 0, 94, 0.5);
  box-sizing: border-box;
  box-shadow: 0 15px 25px rgba(255, 32, 121, 0.6);
  border-bottom: solid 0.15rem var(--fourth-color);
}

form {
  text-align: center;
  align-items: center;
  color: rgb(68, 11, 212);
  text-shadow: 0 0 0.125em hsl(0 0% 100% / 0.1), 0 0 0.25em rgb(5, 58, 60);
  background: rgba(0, 0, 0, 0.5);
  box-sizing: border-box;
  /* box-shadow: 0 15px 25px rgba(255,32,121, 0.6); */
  border-radius: 10px;
  width: 40%;
  padding-top: 20px;
  padding-bottom: 20px;
  margin-top: 50px;
  margin-bottom: 50px;
}

form h1 {
  padding-bottom: 0.5rem;
  margin-bottom: 0.5rem;
  border-bottom: solid 2px rgb(68, 11, 212);
}
label {
  font-size: 1.1rem;
}

input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid var(--primary-color);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
  background-color: rgba(253, 253, 253, 0.575);
  width: 75%;
  margin-bottom: 1rem;
}

input:focus {
  border: 1px solid var(--secondary);
  box-shadow: 0 0 0.25rem rgba(0, 0, 0, 0.5);
}

textarea {
  background-color: rgba(253, 253, 253, 0.575);
  width: 80%;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

p.error-text {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}

.contactBtn {
  background-color: black;
  position: relative;
  padding-top: 10px 30px;
  margin: 15px 15px;
  color: var(--fourth-color);
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 2px;
  font-size: 20px;
  overflow: hidden;
  transition: 0.5s;
  /* border: none; */
}

.contactBtn:hover {
  background: var(--fourth-color);
  color: #111;
  box-shadow: 0 0 50px var(--fourth-color);
  transition-delay: 0.2s;
}

.contactBtn:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 10px;
  height: 10px;
  border-top: 2px solid var(--fourth-color);
  border-left: 2px solid var(--fourth-color);
  transition: 0.5s;
  transition-delay: 0.2s;
}

.contactBtn:hover:before {
  width: 100%;
  height: 100%;
  transition-delay: 0s;
}

.contactBtn::after {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  width: 10px;
  height: 10px;
  border-bottom: 2px solid var(--fourth-color);
  border-right: 2px solid var(--fourth-color);
  overflow: hidden;
  transition: 0.5s;
  transition-delay: 0.2s;
}

.contactBtn:hover:after {
  width: 100%;
  height: 100%;
  transition-delay: 0s;
}

/*-END CONTACT-*/

/* MEDIA QUERY FOR TABLETS AND SMALLER */
@media screen and (max-width: 575px) {
  form {
    width: 80%;
  }
  input {
    width: 60%;
  }
  textarea {
    width: 60%;
  }
  .contact {
    flex-wrap: wrap;
    flex-direction: column;
    text-align: center;
  }
}
